  color: rgb(var(--color-movies));
  border: 1px solid rgba(251, 113, 133, 0.4);
}
.badge-tv {
  background: rgba(192, 132, 252, 0.25);
  color: rgb(var(--color-tv));
  border: 1px solid rgba(192, 132, 252, 0.4);
}
.badge-books {
  background: rgba(74, 222, 128, 0.25);
  color: rgb(var(--color-books));
  border: 1px solid rgba(74, 222, 128, 0.4);
}
.badge-music {
  background: rgba(251, 191, 36, 0.25);
  color: rgb(var(--color-music));
  border: 1px solid rgba(251, 191, 36, 0.4);
}
.interactive {
  transition: all var(--transition-bounce);
  cursor: pointer;
}
.interactive:hover {
  transform: translateY(-4px) scale(1.02);
  box-shadow: var(--shadow-glow);
}
.interactive:active {
  transform: translateY(-2px) scale(1);
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.25rem;
  border-radius: var(--radius-xl);
  transition: all var(--transition-bounce);
  cursor: pointer;
  border: none;
  outline: none;
  position: relative;
  overflow: hidden;
}
.btn::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2) 0%, transparent 50%);
  opacity: 0;
  transition: opacity var(--transition-default);
}
.btn:hover::before {
  opacity: 1;
}
.btn:focus-visible {
  outline: 3px solid rgb(var(--color-accent));
  outline-offset: 2px;
}
.btn-primary {
  background: linear-gradient(135deg, rgb(var(--color-primary)) 0%, rgb(var(--color-secondary)) 100%);
  color: white;
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.4);
}
.btn-primary:hover {
  transform: translateY(-3px) scale(1.02);
  box-shadow: 0 8px 30px rgba(139, 92, 246, 0.5);
}
.btn-secondary {
  background: rgb(var(--bg-elevated));
  color: rgb(var(--text-primary));
  border: 2px solid rgb(var(--border-default));
}
.btn-secondary:hover {
  background: rgb(var(--bg-overlay));
  border-color: rgb(var(--color-primary-light));
  box-shadow: var(--shadow-glow);
}
.btn-ghost {
  background: transparent;
  color: rgb(var(--text-secondary));
}
.btn-ghost:hover {
  background: rgb(var(--bg-elevated));
  color: rgb(var(--text-primary));
}
.input {
  width: 100%;
  padding: 0.75rem 1.25rem;
  font-size: 0.875rem;
  line-height: 1.5rem;
  background: rgba(var(--bg-base), 0.6);
  border: 2px solid rgb(var(--border-subtle));
  border-radius: var(--radius-xl);
  color: rgb(var(--text-primary));
  transition: all var(--transition-default);
}
.input::placeholder {
  color: rgb(var(--text-muted));
}
.input:focus {
  outline: none;
  border-color: rgb(var(--color-primary));
  box-shadow: 0 0 0 4px rgba(139, 92, 246, 0.25);
}
.card {
  background: rgba(var(--bg-surface), 0.8);
  border: 1px solid rgba(167, 139, 250, 0.2);
  border-radius: var(--radius-2xl);
  overflow: hidden;
  backdrop-filter: blur(8px);
}
.card-hover {
  transition: all var(--transition-bounce);
}
.card-hover:hover {
  border-color: rgba(167, 139, 250, 0.4);
  box-shadow: 0 10px 40px rgba(139, 92, 246, 0.3);
  transform: translateY(-6px) scale(1.01);
}
.float {
  animation: float 6s ease-in-out infinite;
}
.float-delayed {
  animation: float 6s ease-in-out infinite 2s;
}
.float-slow {
  animation: float 8s ease-in-out infinite 1s;
}
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-track {
  background: rgb(var(--bg-base));
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, rgb(var(--color-primary)) 0%, rgb(var(--color-secondary)) 100%);
  border-radius: var(--radius-full);
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, rgb(var(--color-primary-light)) 0%, rgb(var(--color-secondary-light)) 100%);
}
@keyframes twinkle {
  0%,
  100% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.8;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0) rotate(0deg);
  }
  25% {
    transform: translateY(-10px) rotate(2deg);
  }
  50% {
    transform: translateY(-5px) rotate(0deg);
  }
  75% {
    transform: translateY(-15px) rotate(-2deg);
  }
}
@keyframes gradient-rotate {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes pulse-glow {
  0%,
  100% {
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.4);
  }
  50% {
    box-shadow: 0 0 40px rgba(139, 92, 246, 0.6), 0 0 60px rgba(236, 72, 153, 0.3);
  }
}
@keyframes wiggle {
  0%,
  100% {
    transform: rotate(-3deg);
  }
  50% {
    transform: rotate(3deg);
  }
}
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
.animate-fade-in {
  animation: fadeIn var(--transition-default) ease-out;
}
.animate-slide-up {
  animation: slideUp var(--transition-slow) ease-out;
}
.animate-slide-down {
  animation: slideDown var(--transition-slow) ease-out;
}
.animate-scale-in {
  animation: scaleIn var(--transition-default) ease-out;
}
.animate-bounce {
  animation: bounce 2s ease-in-out infinite;
}
.animate-pulse-glow {
  animation: pulse-glow 3s ease-in-out infinite;
}
.animate-wiggle {
  animation: wiggle 0.5s ease-in-out;
}
.skeleton {
  position: relative;
  overflow: hidden;
  background: rgb(var(--bg-elevated));
  border-radius: var(--radius-lg);
}
.skeleton::after {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(90deg,
      transparent,
      rgba(167, 139, 250, 0.1),
      transparent);
  animation: shimmer 2s infinite;
}
.emoji-float {
  position: absolute;
  font-size: 2rem;
  opacity: 0.6;
  pointer-events: none;
  animation: float 8s ease-in-out infinite;
}
@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.animate-slide-in {
  animation: slideInRight var(--transition-slow) ease-out;
}
